h2 {
  font-size: 50px;
  margin: 0;
  padding-top: 5%;
}

.movie-details {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0px 0px 100px 70px inset;
}

.movie-details-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 40vw;
  font-size: 1em;
  background-color: whitesmoke;
  color: black;
  border-radius: 2%;
  opacity: .8;
}

.details {
  display: flex;
  font-size: 15px;
  margin: 5px;
}

.trailer-video {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin-top: 2%;
}

.hidden {
  display: none;
}

@media screen and (max-width: 1000px) {
  .trailer-video {
    width: 100%;
    height: 100%;
  }

}

@media screen and (max-width: 720px) {
  .trailer-video {
    display: none;
  }
  .hidden {
    display: inline;
  }
}
